@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-size: 14px;
  line-height: 1.2;
  font-weight: 400;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  text-decoration: none;
  color: #000000;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow-x: hidden;
}
.wrapper .header {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
}

.header {
  min-width: 375px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.05);
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .header__wrapper {
    flex-wrap: nowrap;
  }
}

.header__logo {
  flex-shrink: 0;
}

.header__search {
  order: 1;
  position: relative;
  width: 100%;
}
.header__search::before {
  position: absolute;
  top: 0;
  right: -20px;
  left: -20px;
  content: "";
  border-top: 1px solid #bbc4d6;
}
@media (min-width: 768px) {
  .header__search {
    order: initial;
    width: initial;
    flex-grow: 1;
    margin: 0 50px;
    border: 0;
  }
  .header__search::before {
    content: initial;
  }
}
@media (min-width: 1280px) {
  .header__search {
    margin: 0 60px;
  }
}

.header__user {
  flex-shrink: 0;
}

.logo {
  padding: 17px 0 16px;
}
@media (min-width: 768px) {
  .logo {
    padding: 25px 0;
  }
}

.logo__img {
  width: 135px;
  height: 30px;
}

.search {
  display: flex;
  flex-direction: row-reverse;
}

.search__fieldset {
  flex-grow: 1;
  margin: 0;
  padding: 0;
  border: 0;
}

.search__input {
  width: 100%;
  padding: 14px 5px 12px;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-size: 14px;
  line-height: 1.14;
  font-weight: 400;
  color: #000000;
  border: 0;
}
.search__input::placeholder {
  color: #bbc4d6;
}
@media (min-width: 768px) {
  .search__input {
    padding: 11px 4px;
    background-position: center left 12px;
    border: 1px solid #ebebf1;
    border-left: 0;
    border-radius: 0 5px 5px 0;
  }
}

.search__button {
  flex-shrink: 0;
  width: 24px;
  padding: 0;
  background-color: transparent;
  background-image: url("../img/svg/search-icon.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 18px 18px;
  border: 0;
  cursor: pointer;
}
@media (min-width: 768px) {
  .search__button {
    width: 35px;
    background-position: center right 4px;
    border: 1px solid #ebebf1;
    border-right: 0;
    border-radius: 5px 0 0 5px;
  }
}

.user {
  position: relative;
}
.user::after {
  display: block;
  width: 24px;
  height: 24px;
  margin-top: 6px;
  content: "";
  background-image: url("../img/svg/user-icon.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
@media (min-width: 768px) {
  .user {
    display: flex;
    align-items: center;
    padding: 20px;
    padding-right: 0;
  }
  .user::before {
    position: absolute;
    content: "";
    top: 0;
    bottom: 0;
    left: 0;
    width: 1px;
    background-color: #ebebf1;
  }
  .user::after {
    content: initial;
  }
}

.user__name {
  display: none;
  margin-right: 10px;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-size: 14px;
  line-height: 1.2;
  font-weight: 400;
  letter-spacing: 0.01em;
  color: #768298;
}
@media (min-width: 768px) {
  .user__name {
    display: block;
  }
}

.user__avatar {
  display: none;
  width: 40px;
  height: 40px;
  object-fit: cover;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .user__avatar {
    display: block;
  }
}

.partners {
  min-width: 375px;
  padding: 70px 0;
}

.partners__title {
  margin: 0;
  margin-bottom: 30px;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-size: 28px;
  line-height: 1.6;
  font-weight: 500;
  text-transform: uppercase;
  color: #121920;
}
@media (min-width: 1280px) {
  .partners__title {
    font-size: 36px;
  }
}

.partners__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .partners__list {
    flex-direction: row;
    flex-wrap: wrap;
    margin-right: -20px;
  }
}
@media (min-width: 1280px) {
  .partners__list {
    margin-right: -30px;
  }
}

.partners__item {
  aspect-ratio: 335/140;
  width: 100%;
  border: 1px solid #ebebf1;
  border-radius: 5px;
}
@media (min-width: 768px) {
  .partners__item {
    aspect-ratio: 229/120;
    width: 30.6%;
  }
}
@media (min-width: 1280px) {
  .partners__item {
    aspect-ratio: 288/120;
    width: 22.6%;
  }
}

.partners__item:not(:first-child):not(:last-child) {
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .partners__item:not(:first-child):not(:last-child) {
    margin-right: 20px;
  }
}
@media (min-width: 1280px) {
  .partners__item:not(:first-child):not(:last-child) {
    margin-right: 30px;
    margin-bottom: 30px;
  }
}

.partners__item:nth-child(2) {
  margin-top: 20px;
}
@media (min-width: 768px) {
  .partners__item:nth-child(2) {
    margin-top: 0;
    margin-left: 20px;
  }
}
@media (min-width: 1280px) {
  .partners__item:nth-child(2) {
    margin-left: 30px;
  }
}

.partners__link {
  display: block;
  width: 100%;
  height: 100%;
  padding: 24px;
}
@media (min-width: 768px) {
  .partners__link {
    padding: 14px;
  }
}
@media (min-width: 1280px) {
  .partners__link {
    padding: 27px;
  }
}
@media (hover: hover) {
  .partners__link:hover {
    box-shadow: -3px 0 40px rgba(0, 0, 0, 0.1), 0 0 10px rgba(164, 164, 164, 0.03);
  }
}
.partners__link:focus {
  box-shadow: -3px 0 40px rgba(0, 0, 0, 0.1), 0 0 10px rgba(164, 164, 164, 0.03);
}

.partners__img {
  min-width: 100%;
  min-height: 100%;
  width: 285px;
  height: 90px;
  object-fit: contain;
}
@media (min-width: 768px) {
  .partners__img {
    width: 199px;
    height: 90px;
  }
}
@media (min-width: 1280px) {
  .partners__img {
    width: 234px;
    height: 64px;
  }
}